.statistics
{
    background-color: #88cdf8;

}


.statList
{
         display: flex;
         justify-content: space-between;
padding: 18px;
padding-bottom: 40px;
         }

.item
        {
            box-shadow: inset 0 0 7px 4px rgba(255,255,255,.5);

            width: 320px;
               height: 120px;
           background-color: #87CEFA;
              border-radius: 20px/60px;
              display: flex;
              flex-direction: column;
            align-items: center;
            justify-content: center;
           color: white
        }
.title
{
    color: white;
    display: flex;
    justify-content: center;
    margin: 0;
    margin-top: 16px;
    padding: 0;
}


