.transactionHistory
{
    box-shadow: inset 0 0 7px 4px rgba(255,255,255,.5);
    width: 999px;
      margin: auto;
    background-color: #87CEFA;
    border-radius: 20px/60px;
    color: white;
	padding-bottom: 100px;
	margin-top: 100px;
}
thead
{
    background-color: #2e92d1;
}
table {
	width: 100%;
	border: none;
	margin-bottom: 20px;
}
.transactionHistory thead th {
	font-weight: bold;
	text-align: left;
    border: none;
	padding: 10px 15px;
	font-size: 20px;
}
.transactionHistory thead tr th:first-child {
    border-radius: 20px/60px;
}
.transactionHistory thead tr th:last-child {
    border-radius: 20px/60px;
}
.transactionHistory tbody td {
	text-align: left;
	border: none;
	padding: 10px 15px;
	font-size: 20px;
	vertical-align: top;
}
.transactionHistory tbody tr:nth-child(even){
	background: #4f60f5;
}
.transactionHistory tbody tr td:first-child {
    border-radius: 20px/60px;
}
.transactionHistory tbody tr td:last-child {
    border-radius: 20px/60px;
}
